function mappingMarker(type: string) {
  if (type === "tourist_attraction")
    return MARKERS_PATHS.TOURIST_ATTRACTION_MARKER;
  else if (type === "shopping") return MARKERS_PATHS.SHOPPING_MARKER;
  else if (type === "experience") return MARKERS_PATHS.EXPERIENCE_MAEKER;
  else if (type === "accommodation") return MARKERS_PATHS.ACCOMMODATION_MARKER;
  else if (type === "restaurant") return MARKERS_PATHS.RESTAURANT_MARKER;
  else return MARKERS_PATHS.STAR_MARKER;
}

const containerStyle = {
  width: "100vw",
  height: "100vh",
};

const center = {
  lat: 35.17013,
  lng: 129.12848,
};

function MyMapComponent() {
  const [course, setCourse] = useState<Course[]>([]);
  const GOOGLE_MAPS_API_KEY = "" + process.env.REACT_APP_GOOGLE_MAPS_API_KEY;
  const { isLoaded } = useJsApiLoader({
    googleMapsApiKey: GOOGLE_MAPS_API_KEY, // 여기에 API 키 입력
    libraries: ["places"],
  });

  useEffect(() => {
    setCourse(data);
  }, []);

  const [directions, setDirections] =
    useState<google.maps.DirectionsResult | null>(null);

  const calculateRoute = useCallback((origin: any, destination: any) => {
    const directionsService = new google.maps.DirectionsService();

    directionsService.route(
      {
        origin,
        destination,
        travelMode: google.maps.TravelMode.TRANSIT, // 운전 경로를 선택 (WALKING, BICYCLING, TRANSIT 가능)
      },
      (result, status) => {
        if (status === "OK" && result) {
          setDirections(result);
        } else {
          console.error(`error fetching directions ${result}`);
        }
      }
    );
  }, []);

  return isLoaded ? (
    <div>
      <GoogleMap mapContainerStyle={containerStyle} center={center} zoom={12}>
        {directions && (
          <>
            {/* <Marker position={origin} icon={mappingMarker("star")} />
            <Marker position={destination} icon={mappingMarker("star")} /> */}
            <DirectionsRenderer
              directions={directions}
              options={{ suppressMarkers: true }}
            />
          </>
        )}
        {/* 카테고리에 따라 마커 표시 */}
        {course &&
          course.map((location, i) => (
            <Marker
              key={i}
              position={{ lat: location.latitude, lng: location.longitude }}
              icon={mappingMarker(location.type)} // 카테고리에 맞는 아이콘 설정
            />
          ))}
      </GoogleMap>
      <ButtonWrapper>
        <button
          onClick={() =>
            calculateRoute(
              { lat: 35.07885, lng: 129.04402 },
              { lat: 35.092648, lng: 129.03255 }
            )
          }
        >
          경로 그리기 1 (흰여울문화마을 → 깡깡이 예술마을)
        </button>

        <button
          onClick={() =>
            calculateRoute(
              { lat: 35.092648, lng: 129.03255 },
              { lat: 35.243847, lng: 129.21524 }
            )
          }
        >
          경로 그리기 2 (깡깡이 예술마을 → 기장시장)
        </button>
        <button
          onClick={() =>
            calculateRoute(
              { lat: 35.243847, lng: 129.21524 },
              { lat: 35.17013, lng: 129.12848 }
            )
          }
        >
          경로 그리기 3 (기장시장 → 키자니아)
        </button>
        <button
          onClick={() =>
            calculateRoute(
              { lat: 35.17013, lng: 129.12848 },
              { lat: 35.12819599, lng: 128.8539747 }
            )
          }
        >
          경로 그리기 4 (키자니아 → 베스티호텔)
        </button>

        <button
          onClick={() =>
            calculateRoute(
              { lat: 35.12819599, lng: 128.8539747 },
              { lat: 35.177387, lng: 128.95245 }
            )
          }
        >
          경로 그리기 5 (베스티호텔 → 만드리곤드레밥)
        </button>
      </ButtonWrapper>
    </div>
  ) : (
    <div>지도 로딩 중...</div>
  );
}

export default React.memo(MyMapComponent);

